<template>
  <div class="login">
    <!-- 头部 - NavBar -->
    <van-nav-bar title="会员登录" right-text="注册" left-arrow @click-left="$router.go(-1)"></van-nav-bar>
    <!-- 主体 -->
    <div class="container">
      <div class="title">
        <h3>手机号注册</h3>
        <p>未注册的手机号登陆后将自动注册</p>
      </div>

      <div class="form">
        <div class="form-item">
          <input v-model="mobile" class="inp" maxlength="11" placeholder="请输入手机号">
        </div>
        <div class="form-item">
          <input v-model="picCode" class="inp" maxlength="5" placeholder="请输入图形验证码" type="text">
          <img v-if="picUrl" :src="picUrl" alt="" @click="handleClick">
        </div>
        <div class="form-item">
          <input v-model="msgCode" class="inp" placeholder="请输入短信验证码" type="text">
          <button @click="getCode">
            {{ second === 60 ? '获取验证码' : `${second}秒后重新获取` }}
          </button>
        </div>

        <div class="login-btn" @click="login">登录</div>
      </div>
    </div>
  </div>
</template>

<script>
import { getPicCode, getMsgCode, codeLogin } from '@/api/login'
import { Toast } from 'vant'

export default {
  name: 'LoginIndex',
  data () {
    return {
      picCode: '', // 用户输入的图形验证码
      picKey: '', // 将来请求传递的图形验证码唯一标识
      picUrl: '', // 存储请求渲染的图片地址
      totalSecond: 60, // 倒计时的总秒数
      second: 60, // 当前秒数，开定时器对second
      timer: null, // 定时器
      mobile: '', // 手机号
      msgCode: '' // 短信验证码
    }
  },
  async created () {
    this.getPicCode()
  },
  methods: {
    // 获取图形验证码
    async getPicCode () {
      const { data: { base64, key } } = await getPicCode()
      this.picUrl = base64
      this.picKey = key

      // Toast('获取图形验证码成功')
      // this.$toast.success('获取图形验证码成功')
    },
    handleClick () {
      this.getPicCode().then(() => {
        Toast('获取图形验证码成功')
      }).catch(() => {
        Toast('获取图形验证码失败')
      })
    },
    // 校验手机号和图形验证码是否合法
    validFn () {
      // 1. 手机号是否合法
      const reg = /^1[3-9]\d{9}$/
      if (!reg.test(this.mobile)) {
        Toast('请输入正确的手机号')
        return false
      }
      // 2. 图形验证码是否合法
      if (!/^\w{4}$/.test(this.picCode)) {
        Toast('请输入正确的图形验证码')
        return false
      }
      return true
    },
    // 获取短信验证码
    async getCode () {
      if (!this.validFn()) return
      if (!this.timer && this.second === this.totalSecond) {
        // 1. 发送请求
        await getMsgCode(this.mobile, this.picKey, this.picCode)
        Toast('短信验证码发送成功，请注意查收')
        // 2. 开启倒计时
        this.timer = setInterval(() => {
          this.second--
          if (this.second <= 0) {
            clearInterval(this.timer)
            this.timer = null
            this.second = this.totalSecond
          }
        }, 1000)
      }
    },

    // 登录
    async login () {
      if (!this.validFn()) return
      // if (!/^\d{6}$/.test(this.msgCode)) {
      //   this.$toast('请输入正确的短信验证码')
      //   return
      // }

      const res = await codeLogin(this.mobile, this.msgCode)
      console.log('登录响应', res)

      this.$store.commit('user/setUserInfo', res.data)
      console.log('登录请求', res)
      this.$toast('登录成功')
      // 跳转到首页
      const url = this.$route.query.backUrl || '/'
      this.$router.replace(url)
    },
    destroyed () {
      // 清除定时器
      if (this.timer) {
        clearInterval(this.timer)
        this.timer = null
      }
    }
  }
}
</script>

<style lang="less" scoped>
.container {
  padding: 49px 29px;

  .title {
    margin-bottom: 20px;
    h3 {
      font-size: 26px;
      font-weight: normal;
    }
    p {
      line-height: 40px;
      font-size: 14px;
      color: #b8b8b8;
    }
  }

  .form-item {
    border-bottom: 1px solid #f3f1f2;
    padding: 8px;
    margin-bottom: 14px;
    display: flex;
    align-items: center;
    .inp {
      display: block;
      border: none;
      outline: none;
      height: 32px;
      font-size: 14px;
      flex: 1;
    }
    img {
      width: 94px;
      height: 31px;
    }
    button {
      height: 31px;
      border: none;
      font-size: 13px;
      color: #cea26a;
      background-color: transparent;
      padding-right: 9px;
    }
  }

  .login-btn {
    width: 100%;
    height: 42px;
    margin-top: 39px;
    background: linear-gradient(90deg,#ecb53c,#ff9211);
    color: #fff;
    border-radius: 39px;
    box-shadow: 0 10px 20px 0 rgba(0,0,0,.1);
    letter-spacing: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
</style>
